import React, { useEffect } from "react";
import useTable from "../../controls/useTable";
import { Button, InputAdornment, Paper, TextField, Toolbar } from "@mui/material";

import { InventorySection } from "./InventorySection";
import {  Search } from "@mui/icons-material";
import moment from "jalali-moment";
import ReactHTMLTableToExcel from "react-html-table-to-excel";


const headCells = [
  { id: "expand", label: "", disableSorting: true },
  { id: "inventorys", label: "" },
  { id: "price", label: "" },
  { id: "qty", label: "" },
];

export const InventoryTbl = (props) => {
  const [expand,setExpand]=React.useState(false);
  const [records,setRecords]=React.useState([]||props.inventory);

  const [filterFn, setFilterFn] = React.useState({
    fn: (item) => {
      return item;
    },
  });
  useEffect(() => {
    if (props.inventory) setRecords(props.inventory);
  }, [props.inventory]);

  const { TblContainer, TblHead, recordsAfterPagingAndSorting } =
    useTable(records, headCells, filterFn,setFilterFn);

    const handleSearch = (e) => {
      setFilterFn({
        fn: (items) => {
          if (e.target.value === "") return items;
          else return props.inventory.filter((x) => x.IName.includes(e.target.value));
        },
      });
    };
  return (
    <Paper sx={{ margin: "5rem" }}>
      <Toolbar>
        <TextField
          style={{ direction: "rtl" }}
          label=""
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <Search />
              </InputAdornment>
            ),
          }}
          onChange={handleSearch}
        />
        <ReactHTMLTableToExcel
          style={{ position: "absolute", right: "10px", dir: "rtl" }}
          id="test-table-xls-button"
          className="download-table-xls-button"
          table="table-to-xls"
          filename={String(moment().format("jYYYY/jMM/jDD-HH:mm"))}
          sheet="   "
          buttonText="Excel"
        />
        <Button
              style={{ position: "absolute", right: "10px" }}
              variant="outlined"
              color="error"
              onClick={() => setExpand(perv=>!perv)}
            >
                
            </Button>
      </Toolbar>
      <TblContainer m={5} id="table-to-xls">
        <TblHead />

        {recordsAfterPagingAndSorting()?.map((item) => (
          <InventorySection key={item.id} item={props.data} inventory={item} expand={expand} />
        ))}
      </TblContainer>
    </Paper>
  );
};
